} */
.Login {
  width: 360px;
  height: 400px;
  /* padding: 8% 0 0; */
  margin: 10px auto;
}

.Form {
  position: relative;
  z-index: 1;
  background: #ffffff;
  max-width: 360px;
  margin: 0 auto 100px;
  padding: 30px;
  text-align: center;
  /* position: absolute; */
  -webkit-box-shadow: 0 0 20px 0 rgba(0, 0, 0, 0.2), 0 5px 5px 0 rgba(0, 0, 0, 0.24);
          box-shadow: 0 0 20px 0 rgba(0, 0, 0, 0.2), 0 5px 5px 0 rgba(0, 0, 0, 0.24);
}
.Form input {
  outline: 0;
  /* background: #f2f2f2; */
  border: 0;
  /* margin-left: 29px; */
  /* margin-bottom: 6px; */
  padding: 20px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  font-size: 18px;
  font-family: "Open Sans";
}

.Form h2 {
  color: rgba(8, 18, 168, 0.932);
  font-style: inherit;
  font-weight: bolder;
  text-align: center;
  margin-top: 0px;
  padding: 5px 5px 0px 0px;
  font-size: x-large;
  font-weight: bolder;
  /* box-sizing: content-box;
  width: 80%; */
  /* box-sizing: content-box;
  border: 1px solid black; */
  border-radius: 10px;
}
@media (min-width: 600px) {
  .Login {
    width: 400px;
  }
}

.link a {
  color: lightskyblue;
  height: 100%;
  /* margin-bottom: 5px;
  margin-right: 8px; */
  padding: 0px;
  text-decoration: none;
  /* border-bottom: 4px solid transparent; */
  display: block;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  font-size: 15px;
}
.Button {
  position: relative;
  text-align: right;
}
.left {
  /* margin-left: 5px; */
  position: relative;
  text-align: left;
}
.Forgot {
  position: relative;
  text-align: right;
}
.link a:hover,
.link a:active {
  color: darkblue;
}



