.User {
  margin: 0;
  padding-top: 10px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  list-style: none;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
}

body {
  background-color: #f3f3f3;
}

.wrapper {
  /* position: absolute; */
  /* top: 50%;
  left: 50%;
  transform: translate(-50%, -50%); */
  width: 75%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-flow: row;
      flex-flow: row;
  -webkit-box-shadow: 0 1px 20px 0 rgba(206, 211, 212, 0.08);
          box-shadow: 0 1px 20px 0 rgba(206, 211, 212, 0.08);
}

.left {
  width: 25%;
  background: lightblue;
  padding: 30px 25px;
  border-top-left-radius: 5px;
  border-bottom-left-radius: 5px;
  text-align: center;
  color: #fff;
}

.wrapper .right {
  width: 75%;
  background: #fff;
  padding: 30px 25px;
  border-top-right-radius: 5px;
  border-bottom-right-radius: 5px;
}

.wrapper .right .info,
.wrapper .right .projects {
  margin-bottom: 25px;
}

.wrapper .right .info h3,
.wrapper .right .projects h3 {
  margin-bottom: 15px;
  padding-bottom: 5px;
  border-bottom: 1px solid #e0e0e0;
  color: #353c4e;
  text-transform: capitalize;
  letter-spacing: 2px;
}

.wrapper .right .info_data,
.wrapper .right .projects_data {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
}

.wrapper .right .info_data .data,
.wrapper .right .projects_data .data {
  width: 45%;
}

.wrapper .right .info_data .data h4,
.wrapper .right .projects_data .data h4 {
  color: #353c4e;

  margin-bottom: 5px;
}

.wrapper .right .info_data .data p,
.wrapper .right .projects_data .data p {
  font-size: 15px;
  margin-bottom: 10px;
  color: #919aa3;
}

.wrapper .social_media ul {
  display: -ms-flexbox;
  display: flex;
}

.wrapper .social_media ul li {
  width: 45px;
  height: 45px;
  background: -webkit-gradient(linear, left top, right top, from(#01a9ac), to(#01dbdf));
  background: -webkit-linear-gradient(left, #01a9ac, #01dbdf);
  background: -o-linear-gradient(left, #01a9ac, #01dbdf);
  background: linear-gradient(to right, #01a9ac, #01dbdf);
  margin-right: 10px;
  border-radius: 5px;
  text-align: center;
  line-height: 45px;
}

@media (max-width: 500px) {
  .wrapper {
    margin-top: 150px;
    left: 45%;
    -ms-flex-flow: column;
        flex-flow: column;
    -webkit-box-shadow: none;
            box-shadow: none;
  }
  .left {
    width: 95%;
    border-top-right-radius: 5px;
    border-bottom-right-radius: none;
    border-bottom-left-radius: none;
  }
  .wrapper .right {
    width: 95%;
    border-top-right-radius: none;
    border-top-left-radius: none;
  }
}



