.toolbar {
  height: 55px;
  width: 100vw;
  position: fixed;
  top: 0;
  left: 0;
  background-color: rgb(51, 48, 48);
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
  -ms-flex-align: center;
      align-items: center;
  padding: 0 20px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  z-index: 90;
}
.Logo {
  height: 10%;
}
.header {
  color: rgb(255, 81, 0);
  font-size: 35px;
}
.toolbar nav {
  height: 100%;
}
@media (max-width: 499px) {
  .DesktopOnly {
    display: none;
  }
}



